/**********
 * Source file: apollo-basics.scss
 *
 * Basic font size, margin definitions.
 */
* {
  border-radius: 0;
}

body {
  background-color: #dddddf;
  color: #555;
  font-weight: normal;
  font-size: 13px;
  font-family: "Open Sans", sans-serif;
  line-height: 1.53846;
}

#apollo-page {
  background-color: #fff;
}

/* Headlines */
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  margin-top: 5px;
  color: #555;
  text-shadow: none;
  font-weight: 300;
  font-family: "Prompt", sans-serif;
}

.ap-plain h3,
.ap-plain .h3,
.ap-plain h4,
.ap-plain .h4,
.ap-plain h5,
.ap-plain .h5,
.ap-plain h6,
.ap-plain .h6 {
  margin-bottom: 5px;
}

h1,
.h1 {
  font-size: 28px;
  line-height: 35px;
}

h2,
.h2 {
  font-size: 24px;
  line-height: 30px;
}

h3,
.h3 {
  font-size: 20px;
  line-height: 25px;
}

h4,
.h4 {
  font-size: 18px;
  line-height: 23px;
}

h5,
.h5 {
  font-size: 18px;
  line-height: 23px;
}

h6,
.h6 {
  font-size: 18px;
  line-height: 23px;
}

/* Headline with underline effect */
.headline {
  display: block;
  margin: 10px 0 25px 0;
  border-bottom: 1px dotted #dddddf;
}

.headline h2,
.headline h3,
.headline h4 {
  display: inline-block;
  margin: 0 0 -2px 0;
  padding-bottom: 5px;
  border-bottom: 2px solid #144382;
}

/* Placeholder for the OpenCms toolbar */
#toolbar-placeholder {
  height: 52px;
  background-color: #fff;
}

/* Links */
a,
a:active {
  outline: 0 !important;
  color: #144382;
}

a:focus,
a:hover {
  outline: 0 !important;
  color: #2197db;
  transition: color 0.33s, background-color 0.33s;
}

a,
a:focus {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Extend Bootstrap forms, adjustment for corners and colors */
.input-group .form-control,
select.form-control,
.form-control {
  border-radius: 0;
  box-shadow: none;
}

.input-group-addon {
  border-radius: 0;
  background: #fff;
  color: #555;
}

/* Extend Bootstrap alert, removes the round corners */
.alert {
  border-radius: 0;
}

/* Extend Bootstrap label, used in the Release notes */
span.label {
  padding: 4px 7px;
  border-radius: 0;
  font-weight: 400;
  font-size: 11px;
}

/* Extend Bootstrap badges */
.badge {
  border-radius: 4px !important;
}

span.badge {
  padding: 4px 7px;
  color: #fff;
  font-weight: 400;
}

/* Colored badges */
span.badge-grey-dark {
  background: #7f8691;
  color: #fff;
}

span.badge-grey {
  background: #a8adb4;
  color: #fff;
}

span.badge-grey-light {
  background: #dddddf;
  color: #555;
}

span.badge-grey-lighter {
  background: #eeeef0;
  color: #555;
}

span.badge-violet {
  background: #642f5b;
  color: #fff;
}

span.badge-green {
  background: #00af33;
  color: #fff;
}

span.badge-yellow {
  background: #ffd900;
  color: #fff;
}

span.badge-cyan {
  background: #1bb39a;
  color: #fff;
}

span.badge-blue-dark {
  background: #0d2c56;
  color: #fff;
}

span.badge-blue {
  background: #144382;
  color: #fff;
}

span.badge-blue-light {
  background: #2197db;
  color: #fff;
}

span.badge-orange-dark {
  background: #d9622d;
  color: #fff;
}

span.badge-orange {
  background: #ffa826;
  color: #fff;
}

span.badge-red-dark {
