 * Source file: apollo-navigation.scss
 *
 * Styles for the header, footer and side navigation.
 */
/* The Apollo header */
.ap-header {
  position: relative;
  background: #fff;
}

.ap-header .navbar-collapse {
  position: relative;
  border-top: 0;
}

@media (max-width: 991px) {
  .ap-header .navbar-header {
    float: none;
  }

  .ap-header .navbar-collapse {
    margin-top: 0;
    padding-right: 0;
    padding-left: 0;
  }

  .ap-header .navbar-collapse .container {
    padding-right: 0;
    padding-left: 0;
  }

  .ap-header .navbar-collapse.collapse {
    display: none !important;
  }

  .ap-header .navbar-collapse.collapse.in {
    display: block !important;
    overflow-y: auto !important;
  }

  .ap-header .head-container .col-xs-12,
  .ap-header .head-container .row {
    margin-right: 0;
    margin-left: 0;
  }

  .ap-header .head-container .col-xs-12,
  .ap-header .head-container .container {
    padding-right: 0;
    padding-left: 0;
  }
}

.ap-header .head-navbar {
  border-bottom: solid 2px #dddddf;
  pointer-events: none;
}

@media (min-width: 992px) {
  .ap-header .head {
    position: relative;
  }

  .ap-header .head-navbar {
    position: absolute;
    bottom: 0px;
    width: 100%;
  }

  .ap-header .head-nopull .head-navbar {
    position: relative;
  }
}

.ap-header .editor .head-navbar {
  position: relative;
  border-top: solid 1px #dddddf;
}

.ap-header .navbar-toggle {
  position: relative;
  z-index: 1000;
  float: right;
  padding: 2px 8px 2px 8px;
  border: none;
  border-radius: 0;
  background: #144382;
  color: #fff;
}

.ap-header .navbar-toggle .fa {
  font-size: 28px;
}

@media (min-width: 992px) {
  .ap-header .navbar-toggle {
    display: none;
  }
}

@media (max-width: 991px) {
  .ap-header .navbar-toggle {
    display: block;
    margin: -40px -15px 0 0;
  }
}

@media (max-width: 767px) {
  .ap-header .navbar-toggle {
    margin: -40px -8px 6px 0;
  }
}

@media (max-width: 496px) {
  .ap-header .navbar-toggle {
    margin: -30px -8px 6px 0;
    padding: 1px 6px 1px 6px;
  }

  .ap-header .navbar-toggle .fa {
    font-size: 18px;
    line-height: 22px;
  }
}

.ap-header .navbar-toggle:hover {
  background: #b31b34;
}

@media (max-width: 991px) {
  .ap-header .head > .head-toggle:first-child > .navbar-toggle {
    margin: 0 -15px 0 0;
  }
}

@media (min-width: 992px) {
  .ap-header .head > .head-navbar:nth-child(2) {
    position: relative;
  }
}

/* Remove default bottom margin from head in model group editor */
#ap-modelinfo-border .area-head {
  margin-bottom: 0;
}

/* Responsive navigation */
.ap-header .nav {
  pointer-events: all;
}

.ap-header .nav > li {
  position: relative;
  transition: all 1s ease-out;
}

.ap-header .nav > li > a {
  position: relative;
  bottom: -2px;
  padding: 9px 20px;
  background-color: rgba(255, 255, 255, 0.75);
  color: #5a6069;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 15px;
}

.ap-header .nav > li > a:after {
  position: absolute;
  display: inline-block;
  font-family: FontAwesome;
}

@media (min-width: 992px) {
  .ap-header .nav {
    float: right;
  }

  .ap-header .nav > li > a {
    margin-bottom: 2px;
  }

  .ap-header .nav > li > a:focus {
    border-bottom: 0;
    background-color: rgba(255, 255, 255, 0.75);
  }

  .ap-header .nav > li.dropdown > a {
    cursor: default;
  }

  .ap-header .nav > li.dropdown > a:after {
    top: 10px;
    right: 6px;
    content: "\f107";
  }

  .ap-header .nav > li.active:hover > a,
  .ap-header .nav > li:hover > a {
    margin-bottom: 0;
    border-bottom: 2px solid #144382;
    background-color: #fff;
    color: #144382;
  }

  .ap-header .nav > li.active > a {
    margin-bottom: 0;
    border-bottom: 2px solid #144382;
    background-color: rgba(255, 255, 255, 0.75);
    color: #144382;
  }

  .ap-header .nav > li.active > a:hover {
    color: #144382;
  }

  .ap-header .nav > li.open > .dropdown-menu {
    display: none;
  }

  .ap-header .nav > li:hover > .dropdown-menu {
    display: block;
  }
}

@media (max-width: 991px) {
  .ap-header .nav {
    float: none !important;
    margin: 0;
  }

  .ap-header .nav > li {
    float: none;
  }

  .ap-header .nav > li > a {
    bottom: 0px;
    padding: 9px 10px;
    border-bottom: solid 1px #dddddf;
    background: #fff;
    font-size: 15px;
  }

  .ap-header .nav > li.dropdown > a:after {
    top: 9px;
    right: 10px;
    content: "\f078";
    transition: transform 0.33s;
    -ms-transform: rotate(0);
    transform: rotate(0);
  }

  .ap-header .nav > li.dropdown.open > a:after {
    transition: transform .33s;
    -ms-transform: rotate(-180deg);
    transform: rotate(-180deg);
  }

  .ap-header .nav > li.open > a,
  .ap-header .nav > li.open > a:focus {
