/* Image gallery image enter animation, used when the image gallery is displayed */
.comein {
  -ms-transform: translateY(120px);
  transform: translateY(120px);
  animation: come-in 0.8s ease forwards;
}

.comein:nth-child(even) {
  -ms-transform: translateY(150px);
  transform: translateY(150px);
  animation-duration: 1.0s;
}

@keyframes come-in {
  to {
    transform: translateY(0);
  }
}
/* Zoom overlay effect used by image gallery as well as the 'image zoom' formatter */
.zoom:hover .zoom-overlay .zoom-icon {
  border: 0;
  box-shadow: none;
  opacity: 1;
  -ms-transform: scale(1);
  transform: scale(1);
}

.zoom-overlay {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
  transition: all 0.1s ease-in-out 0s;
}

.zoom-overlay .zoom-icon {
  position: absolute;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: all 0.4s ease-in-out 0s;
  -ms-transform: scale(0);
  transform: scale(0);
}

.zoom-overlay .zoom-icon:before {
  background: transparent;
}

.zoom-overlay .zoom-icon:after {
  background: transparent;
}

.zoom-overlay .zoom-icon i {
  position: absolute;
  top: 50%;
  left: 50%;
  color: #fff;
  font-size: 30px;
  -ms-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

